.DocPage {
    max-height:100vh !important;
    overflow:auto;
}

.DocSummary {
    width:40%;
    float:right;
    
}
.FullText {
    width:55%;
    float:left;
    white-space: pre-wrap;
}

.Both {
    overflow-y: scroll;
    
    border: var(--DF_border1);
    margin:5px;
    max-height:90vh;
}

@media  (max-width: 768px) {
    /* For mobile phones: */
    .Both {
        overflow-x:hidden;
     /*   min-50vwidth:50vh;*/
        max-height:60vh;
    }
  }